.navbar-expand-lg .navbar-nav .nav-link {
    font-size: medium;
    padding-left: 35px;
    color: #fff;
}

.navbar-expand-lg .navbar-collapse{
    height: 40px;
}

.pic{
    height: 200px;
}
.card{
    height: 300px;
    margin-top: 15px;
}
.card-img-top{
    height: 150px;
    
}
.list{
    list-style: none;
}
.list li{
    display: inline;
    padding-left: 20px;
}

@media (max-width: 480px) {
    .container{
        width: 100%;
    }
    .top{
       height: 100px; 
    }
    .logoo{max-width: 20%; height: 70px;
    position: absolute; top: 5%;}

    .navbar-toggler{
        position: absolute;
        left: 80% !important;
        bottom: 100%
    }
    /* #navbarSupportedContent{
        background: red;
    } */
    .nav-link{
        background: red;
        width: 120px;
        text-align: right;
        
 
    }
    .navbar-nav{
        position: absolute;
        left: 70%;
        z-index: 999;
    }

    

   

    
}

@media (max-width:786px) and (min-width:480px) {

    .container{
        width: 100%;
    }
    .top{
       height: 100px; 
    }

    .logoo{max-width: 120px; height: 70px;
        position: absolute; top: 5%;}
    
        .navbar-toggler{
            position: absolute;
            left: 80% !important;
           
        }
        /* #navbarSupportedContent{
            background: red;
        } */
        .nav-link{
            background: red;
            width: 120px;
            text-align: right;
            
     
        }
        .navbar-nav{
            position: absolute;
            top: 70px;
            left: 70%;
            z-index: 999;
        }

    
}